import React, { useState } from "react"
import styled from "styled-components"
import { contactItems } from "../utils/commons"
import FontAwesomeIconWrapper from "./FontAwesomeIconWrapper"
import { rhythm } from "../utils/typography"

const withPrefix = src => (
  `https://krushiraj.github.io${src}#toolbar=0&navpanes=0`
)

const StyledContactMe = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  align-content: center;
  height: 100%;
  width: 100%;

  div {
    margin: auto 0;
    width: 100%;
  }

  .icon-container {
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    justify-content: space-around;
    width: 100%;

    a {
      color: rgba(255, 255, 255, 0.4);
    }

    .underline {
      &:hover {
        color: rgba(255, 255, 255, 1);
      }

      &:active {
        color: #ffffff;
      }

      &:visited {
        color: #ffffff;
      }
    }
  }
`

const Resume = () => {
  const [viewResume, setViewResume] = useState(false)
  const [printResume, setPrintResume] = useState(false)
  const variant = printResume ?
    "Print" : "Web"
  const src = `/KrushiRajTula_Resume${variant}.pdf`
  return <>
    <iframe
      id="resume"
      title="Resume"
      src={withPrefix(src)}
      style={{
        height: viewResume ? "29.7cm" : "0",
        width: "21cm",
        overflow: "auto",
        border: "none",
        margin: `${rhythm(1)} 0`,
      }}
    ></iframe>
    <p>
      You can view my resume by clicking
      <button onClick={() => setViewResume(!viewResume)}>
        here
      </button>.{" "}
      You can also get the{" "}
      <button onClick={() => {
        setPrintResume(!printResume)
        const resume = document.getElementById("resume")
        if (resume.src.match('Print') !== null) {
          console.log("printing resume")
          resume.focus()
          resume.contentWindow.print()
        }
      }}>printable version</button>.
      {`${printResume}, ${viewResume}, ${variant}, ${src}`}
    </p>
  </>
}

export default () =>
  <StyledContactMe>
    <div>
      <p>Feel free to reach me out at:</p>
      <div className="icon-container">
        {contactItems.slice(0, 4).map((item, key) => (
          <FontAwesomeIconWrapper key={key} {...item} size="3x" />
        ))}
      </div>
      <Resume/>
    </div>
  </StyledContactMe>
